import { useState } from 'react'
import { Modal } from 'react-bootstrap'

const wallets = [
  {
    id: 1,
    text: 'Install Metamask',
    image: './assets/images/icons/wallet-metamask-icon.png',
  },
  {
    id: 2,
    text: 'Install Metamask',
    image: './assets/images/icons/wallet-walletconnect-icon.png',
  },
]

interface ModalProps {
  isOpen: boolean
  onDismiss: () => void
}

export function ConnectWalletModal({ isOpen, onDismiss }: ModalProps) {
  const [selectedWalletId, setSelectedWalletId] = useState<number | undefined>()

  return (
    <Modal show={isOpen} onHide={onDismiss}>
      <div className="modal-header">
        <h1 className="modal-title" id="modal-basic-title">
          Connect to a wallet
        </h1>
        <button
          type="button"
          className="close close-modal-button"
          aria-label="Close"
          onClick={onDismiss}
        />
      </div>
      <div className="modal-body">
        <div className="connect-wallet-bridges">
          {wallets.map((wallet) => (
            <button
              className={`no-appearance${
                selectedWalletId === wallet.id ? ' active' : ''
              }`}
              onClick={() => setSelectedWalletId(wallet.id)}
            >
              <span>{wallet.text}</span>
              <img src="{wallet.image}" alt="" />
            </button>
          ))}
          <a href="/" className="link flex justify-content-end">
            Are you lost? click here to get help
          </a>
        </div>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-primary" onClick={onDismiss}>
          Connect
        </button>
      </div>
    </Modal>
  )
}