import { useState } from 'react'
import { Modal } from 'react-bootstrap'

interface ModalProps {
  isOpen: boolean
  onDismiss: () => void
}

function DepositModal({ isOpen, onDismiss }: ModalProps) {
  const [stepIndex, setStepIndex] = useState<number>(0)

  function prev() {
    setStepIndex(stepIndex - 1)
  }
  function next() {
    setStepIndex(stepIndex + 1)
  }

  return (
    <Modal show={isOpen} onHide={onDismiss}>
      <div className="modal-header">
        {stepIndex === 0 && (
          <div className="bag-wizard">
            <div className="bag-wizard-step current">
              <span className="step-counter">1</span>
              <span className="step-name">Deposit</span>
            </div>
            <div className="bag-steps-connector"></div>
            <div className="bag-wizard-step">
              <span className="step-counter">2</span>
              <span className="step-name">Review &amp; Confirm</span>
            </div>
          </div>
        )}
        {stepIndex === 1 && (
          <div className="bag-wizard">
            <div className="bag-wizard-step passed" onClick={prev}>
              <span className="step-counter">1</span>
              <span className="step-name">Deposit</span>
            </div>
            <div className="bag-steps-connector"></div>
            <div className="bag-wizard-step current">
              <span className="step-counter">2</span>
              <span className="step-name">Review &amp; Confirm</span>
            </div>
          </div>
        )}
        {stepIndex === 2 && (
          <div className="bag-wizard">
            <div className="bag-wizard-step passed" onClick={prev}>
              <span className="step-counter">1</span>
              <span className="step-name">Deposit</span>
            </div>
            <div className="bag-steps-connector"></div>
            <div className="bag-wizard-step passed" onClick={prev}>
              <span className="step-counter">2</span>
              <span className="step-name">Review &amp; Confirm</span>
            </div>
          </div>
        )}
        <button
          type="button"
          className="close close-modal-button"
          aria-label="Close"
          onClick={onDismiss}
        ></button>
      </div>
      <div className="modal-body">
        {stepIndex === 0 && (
          <div>
            <form>
              <h1 className="form-group-title">
                Balance: <span className="highlighted ml-10">12,7832.85</span>
                <img className="ml-10" src="/assets/images/logo.svg" alt="" />
              </h1>
              <div className="form-group">
                <label htmlFor="deposit">Enter amount to deposit</label>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control"
                    id="deposit"
                    aria-describedby="deposit"
                    placeholder="Enter an amount"
                  />
                  <button className="btn btn-primary ml-20">max</button>
                </div>
              </div>
              <span className="yellow">
                There will be a 0.3% fee if you withdraw before 40,320 block (~7
                days)
              </span>
            </form>
          </div>
        )}
        {stepIndex === 1 && (
          <div>
            <h1 className="form-group-title left-aligned">
              Balance: <span className="highlighted">12,7832.85</span>
              <img className="ml-10" src="/assets/images/logo.svg" alt="" />
            </h1>
            <span className="yellow">
              There will be a 0.3% fee if you withdraw before 40,320 block (~7
              days)
            </span>
          </div>
        )}
        {stepIndex === 2 && (
          <div>
            <div className="confirmation-screen">
              <img src="/assets/images/icons/check-mark-gradient.svg" alt="" />
              <h1>Thank you for your the Deposit!</h1>
              <span className="confirmation-bottom-text">
                You should have received a confirmation email
              </span>
            </div>
          </div>
        )}
      </div>
      <div className="modal-footer">
        {stepIndex > 0 && (
          <button type="button" className="btn btn-primary" onClick={prev}>
            Back
          </button>
        )}
        {stepIndex < 2 && (
          <button type="button" className="btn btn-primary" onClick={next}>
            Approve
          </button>
        )}
        {stepIndex === 2 && (
          <button type="button" className="btn btn-primary" onClick={onDismiss}>
            Close
          </button>
        )}
      </div>
    </Modal>
  )
}

